import * as React from 'react';
import { Select, SelectOption as Option } from '@ali/we-design';
import { IndentType } from '@ali/4ever-utils';

interface IndentProps {
  locale: Record<string, any>;
  type: IndentType;
  handleIndentTypeChange: Function;
}

export default function SpecialIndent(props: IndentProps) {
  const { locale, handleIndentTypeChange, type = IndentType.none } = props;
  const indentList = [
    { title: locale.noneIndent, key: IndentType.none },
    { title: locale.firstLineIndent, key: IndentType.firstLine },
    { title: locale.hangIndent, key: IndentType.hang },
  ];

  const handleChange = (indent: IndentType) => {
    handleIndentTypeChange(indent);
  };

  return (
    <Select
      className="bi-sidebar-indent"
      value={type}
      onChange={handleChange}
      defaultValue={IndentType.none}
    >
      {
        indentList.map(indent => (
          <Option
            value={indent.key}
            key={indent.key}
          >
            <span>{indent.title}</span>
          </Option>
        ))
      }
    </Select>
  );
}
